import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import { post,tokenpost} from '@/api/user.js';
import { api } from '@/config/common.js';
import cuLoading from '@/components/custom/cu-loading.vue'
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '收款账号 ',
			dataList: [],
			isdeleteDict: ['正常','禁用'],
			accounTypeList2:["","银行账号","微信","支付宝","现金"],
			accounTypeList:{
				'0':"",
				'1':"银行账号",
				'2':"微信",
				'3':"支付宝",
				'4':"现金"
			}
		};
	},
	onLoad(){},
	onShow(){
		if(!uni.getStorageSync('userInfo')){
			uni.reLaunch({
				url:'/pages/my/login/login'
			})
		};
		this.loadData();
	},
	methods: {
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleAdd() {
			console.log('-----');
			uni.navigateTo({
				url:'/pages/my/account/accountadd'
			})
			console.log('-----');
		},
		handleDetail(id) {
			console.log('---222--');
			uni.navigateTo({
				url:'/pages/my/account/accountdetail?id='+id
			})
		},		
		handleEdit(id) {
			console.log('-----');
			uni.navigateTo({
				url:'/pages/my/account/accountedit?id='+id
			})
		},
		loadData(){
			this.$refs.loading.open();
			tokenpost(api.MyCashAccount).then(res => {
				this.$refs.loading.close();
				if (res.status == 200 && res.data.returnCode == '0000') {
				  this.dataList = res.data.data.resultList
				} else {
					this.$api.msg(res.data.returnMessage) 
				}
			}).catch(error => {
				this.$refs.loading.close();
				this.$api.msg('请求失败fail') 
			})
		}
	}
};
